.checklist__counter{
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
    padding: 10px;
    font-size: 20px;
}
.checklist__counter > * + * {
    margin-left: 10px;
} 

@media (max-width: 575px) {
    .checklist__counter {
        font-size: 18px;
    }
}
